
.app__works {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: 4rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border: 1px solid var(--secondary-color); ;
        border-radius: 0.5rem;
        background-color: #ffffff00;
        color: var(--lightBlue-color);
        font-weight: 700;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            // background-color: var(--secondary-color);
            // color: #fff;
            border: 1px solid white;
            box-shadow: inset 0px 0px 2px #fff,
              0px 0px 2px #fff,
              0 0 5px #28d7fe,
              inset 0 0 5px #28d7fe,
              0 0 10px 1px #28d7fe,
              inset 0 0 10px  1px #28d7fe;
        }

        @media screen and (min-width:2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;

        }

    }

    .item-active {
        background-color: transparent;
        color: var(--lightBlue-color);
        border: 1px solid white;
        box-shadow: inset 0px 0px 2px #fff,
          0px 0px 2px #fff,
          0 0 5px #28d7fe,
          inset 0 0 5px #28d7fe,
          0 0 10px 1px #28d7fe,
          inset 0 0 10px  1px #28d7fe;
    }
}

.app__work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item {
        width: 270px;
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border-radius: 0.5rem;
        background-color: #ffffff00;
        color: #000;
        border: 2px solid var(--secondary-color);
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
            box-shadow: 0 0 25px rgba(0, 0, 0, 0.25);
        }

        @media screen and (min-width:2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__work-img {
    width: 100%;
    height: 160px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        // object-fit:fill;
        border: 2px solid var(--lightBlue-color);
        // object-fit: contain;
        object-fit: cover;
    }

    @media screen and (min-width:2000px) {
        height: 280px;
    }
}

.app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;
   

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.5);
        border: 1px solid var(--secondary-color);
        box-shadow: inset 0px 0px 1px #fff,
        0px 0px 1px #fff,
        0 0 1px #28d7fe,
        inset 0 0 1px #28d7fe,
        0 0 1px 1px #28d7fe,
        inset 0 0 1px  1px #28d7fe;
       
        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg {
            width: 50%;
            height: 50%;
            color:var(--secondary-color);
            color:var(--white-color);
        }
    }
}

.app__work-content {
    width: 100%;
    position: relative;
    flex-direction: column;

    h4 {
        margin-top: 1rem;
        line-height: 1.5;
    }

    .app__work-tag {
        position: absolute;
        padding: 0.5rem 1rem;
        border-radius: 10px;
        background-color: #fff;
        background-color: rgba(0, 0, 0, 0.5);
        border: 1px solid var(--lightBlue-color);
        top: -25px;
    }
}